.contact-form-content{
    width: 100%;
}
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.5rem;

}
.name-content{
    display: flex;
    align-items: center;
    grid-gap: 1.5rem;

}
form input, form textarea{
    flex: 1;
    width: 100%;
    font-size: 0.9rem;
    color: #fff;
    background-color: #130f2a;
    border-radius: 0.6rem;
    border: 1.5px solid #6751b9;
    padding: 1rem;
    
}
form button{
    font-size: 1rem;
    font-weight: 500;
    color: #fff;
    border: 1.5px solid #a892fe;
    border-radius: 0.6rem;
    /* background-color:linear-gradient(90deg, #a892fe 0%, #8064e8 100%); */
    background-color: #8064e8;
    padding: 0.7rem;
    transform: all 0.3s ease;
    cursor: pointer;
}
form button:hover{
    color: #a892fe;
    background-color: transparent;
}
@media(max-width:450px){
    .name-content{
        flex-direction: column;
        align-items: flex-start;
    }
    form input, form textarea{
        padding: 0.7rem;
        border-radius: 0.3rem;
    }
    form button{
        font-size:0.5rem;
        padding: 0.7rem;
        border-radius: 0.3rem;
        
    }
}
